











































import {Component, Vue} from 'vue-property-decorator';
import VueButton from './button/VueButton.vue';
import {ToastPosType, VueToast as VueToastType} from '../types/VueToast';
import VueDetail from './code-presentation/VueDetail.vue';
import VueCodePresentation from './code-presentation/VueCodePresentation.vue';

@Component({
  components: {
    VueButton,
    VueDetail,
    VueCodePresentation
  }
})
export default class ToastsTips extends Vue {
  name = 'ToastsTips';

  showToast0() {
    this.$toast('VueToast');
  }

  showToast11() {
    this.$toast(` ${Math.round(Math.random() * 100)} `);
  }

  showToast1() {
    this.$toast('VueToastVueToastVueToastVueToast', {
      propsData: {
        autoCloseDelay: false,
        closeButton: {
          text: ''
        }
      }
    });
  }

  showToast2() {
    this.$toast('VueToast', {
      propsData: {
        autoCloseDelay: false,
        closeButton: {
          text: ''
        }
      }
    });
  }

  popUpToasts() {
    this.$toast('VueToast', {
      propsData: {
        autoCloseDelay: false,
        closeButton: {
          text: '',
          callback(toast: VueToastType) {
            toast.close();
            toast.$toast('', {
              propsData: {
                position: 'middle',
                autoCloseDelay: 2000
              }
            });
          }
        }
      }
    });
  }

  popUpToasts2() {
    this.$toast(`
        <p>
          <i></i><strong></strong>VueToast
        </p>`, {
      propsData: {
        autoCloseDelay: false,
        enableUnsafeHTML: true,
        closeButton: {
          text: '',
          callback(toast: VueToastType) {
            toast.close();
          }
        }
      }
    });
  }

  popUpToasts3() {
    this.$toast(`
        <p>
          <a style="color: seagreen;" href="https://cn.vuejs.org">Vue</a>
        </p>`, {
      propsData: {
        autoCloseDelay: false,
        enableUnsafeHTML: true,
        closeButton: {
          text: '',
          callback(toast: VueToastType) {
            toast.close();
          }
        }
      }
    });
  }

  popUpToasts4(position: keyof ToastPosType) {
    const positionMap = {
      'top': '',
      'middle': '',
      'bottom': '',
    };
    this.$toast(` ${positionMap[position]}`, {
      propsData: {
        position,
        autoCloseDelay: 1200,
        enableUnsafeHTML: true,
        closeButton: {
          text: '',
          callback(toast: VueToastType) {
            toast.close();
          }
        }
      }
    });
  }

}
