
























import {Component, Prop, Vue, Watch} from 'vue-property-decorator';

@Component
export default class VuePopover extends Vue {
  name = 'VuePopover';
  isVisible = false;
  timer: number | null = null;

  clientWidth = document.body.clientWidth;

  /*
  // 
  get wrapperClass() {
    if (this.clientWidth <= 500) {
      return {
        'touchStopWrapper': true
      };
    }
    return {}
  }

  get triggerClass() {
    if (this.clientWidth <= 500) {
      return {
        'touchStopTrigger': true
      };
    }
    return {}
  }
*/

  // prop: [autoCloseDelay, position, trigger, zIndex]
  @Prop({
    type: [Number, Boolean], default: 580, validator(value: false | number): boolean {
      return (value === false) || (value > 0);
    }
  }) autoCloseDelay!: false | number;
  @Prop({
    type: String,
    default: 'top',
    validator(value: string): boolean {
      return ['top', 'bottom', 'left', 'right'].includes(value);
    }
  }) position!: 'top' | 'bottom' | 'left' | 'right';
  @Prop({
    type: String,
    default: 'hover',
    validator(value: string): boolean {
      return ['hover', 'click'].includes(value);
    }
  }) trigger!: 'hover' | 'click';
  @Prop({
    type: Number,
    default: 999,
    required: false
  }) zIndex!: number;

  clearTimer() {
    clearTimeout(this.timer || undefined);
  }

  startTimer() {
    if (this.autoCloseDelay) {
      this.timer = window.setTimeout(() => {
        this.closeEvent();
      }, this.autoCloseDelay);
    }
  }

  // 
  get multiEventOnTrigger() {
    return (this.trigger === 'click'
      ? {
        mouseover: this.clearTimer,
        mouseenter: this.clearTimer,
        touchstart: this.clearTimer,
      }
      : {
        // hover
        // mouseover: this.clearTimer,
      });
  }

  // 
  get multiEventOnContent() {
    return {
      mouseover: this.clearTimer,
      mouseenter: this.clearTimer,
      mouseleave: this.startTimer,
    };
  }

  get ifHover() {
    return this.trigger === 'hover'
      ? 'mouseenter'
      : /* click */ null;
  }

  get keepPopUp() {
    return this.trigger === 'hover'
      ? 'mouseenter'
      : /* click */ 'touchstart';
  }

  //  popover 
  // 
  //  top left
  positionPop() {
    //    body 
    document.body.appendChild(this.$refs.contentWrapper as Node);

    //  popover , 
    const {contentWrapper, triggerWrapper} = this.$refs;
    //    width, height, top, left
    const {
      width: buttonWidth,
      height: buttonHeight,
      top: buttonTop,
      left: buttonLeft
    } = (triggerWrapper as HTMLElement)
      .getBoundingClientRect();
    //   height: popHeight 
    const {height: popHeight} = (contentWrapper as HTMLElement)
      .getBoundingClientRect();
    (contentWrapper as HTMLElement).style.zIndex = `${this.zIndex}`;

    //  
    const positionList = {
      'position-top': {
        top: buttonTop + window.scrollY,
        left: buttonLeft + window.scrollX
      },
      'position-bottom': {
        top: buttonTop + buttonHeight + window.scrollY,
        left: buttonLeft + window.scrollX
      },
      'position-left': {
        // popover  popover1/2
        top: buttonTop + Math.abs(buttonHeight - popHeight) / 2 + window.scrollY,
        left: buttonLeft + window.scrollX
      },
      'position-right': {
        top: buttonTop + Math.abs(buttonHeight - popHeight) / 2 + window.scrollY,
        left: buttonLeft + buttonWidth + window.scrollX
      },
    };

    //  top left
    (contentWrapper as HTMLElement).style.top =
      positionList[`position-${this.position}` as positionListString].top + `px`;
    (contentWrapper as HTMLElement).style.left =
      positionList[`position-${this.position}` as positionListString].left + `px`;

  }

  // 
  closeHandler(e: Event) {
    const contentWrapperElement = this.$refs.contentWrapper as Element;
    //    popover div
    // contentWrapperElement?.contains() vuepress-plugin-typescript not support ?.
    const hasPopover = contentWrapperElement &&
      contentWrapperElement.contains(e.target as Node);
    //    div  document
    if (!hasPopover) {
      this.closeEvent();
    } else {return;}
  }

  get switcher() {
    return this.clientWidth >= 500 ? 'click' : 'touchstart';
  }

  listenToDocument() {
    const trigger = this.clientWidth >= 500 ? 'click' : 'touchstart';
    document.addEventListener(trigger, this.closeHandler);
  }

  rmListenerToDocument() {
    const trigger = this.clientWidth >= 500 ? 'click' : 'touchstart';
    document.removeEventListener(trigger, this.closeHandler);
  }

  //   
  closeEvent() {
    this.isVisible = false;
    this.rmListenerToDocument();
  }

  // 
  openEvent() {
    this.isVisible = true;
    this.onShowPopover();
  }

  //  / popover
  togglePop(event: Event) {
    //  
    //    
    const triggerWrapperElement = this.$refs.triggerWrapper as HTMLElement;
    // triggerWrapperElement?.contains() vuepress-plugin-typescript not support ?.
    if (triggerWrapperElement &&
      triggerWrapperElement.contains(event.target as Node)) {
      // / popover
      this.isVisible = !this.isVisible;
    } else {
      // popover 
      // 
    }

  }

  onShowPopover() {
    this.$nextTick(() => {
      //   body 
      // 
      this.positionPop();
      //     
      setTimeout(() => {
        //  document  click 
        //  300ms 
        this.listenToDocument();
      }, 350);
    });
  }

  //  this.isVisible  
  //  popover  onShowPopover
  //  popover  
  @Watch('isVisible', /*{immediate: true}*/)
  onVisibleChange(newValue: boolean) {
    if (newValue) {
      //  popover  
      this.onShowPopover();
    } else {
      //  popover  
      return;
    }
  }

}
